body{
	background-color:#002f3d;
	margin:0%;
	padding:0%;
}
ul{
	list-style:none;
}
a{
	text-decoration:none;
}
section{
	width:100%;
	height:95vh;
	background-repeat: no-repeat;
	background-size: cover;
	position:relative;
}

button:active{
	transform:scale(1.1);
}
.model{
height: 38%;
position: absolute;
bottom: 0%;
left:70%;
transform:translateX(-70%);
}
